@font-face {
  font-family: SpaceMonkey;
  src: url("subset-SpaceMonkey.34494bb1.woff2") format("woff2"), url("subset-SpaceMonkey.34494bb1.woff2") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

.container {
  position: relative;
}

#golink {
  margin-right: 1em;
}

.item {
  width: 353px;
  height: 310px;
  border: 2px solid #a9a9a9;
  border-radius: 5px;
  font-family: SpaceMonkey;
  font-style: normal;
  font-weight: 500;
  position: absolute;
  left: 0;
  box-shadow: 10px 10px 10px gray;
}

.moving {
  animation-name: float-out;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

.item:before {
  content: "";
  width: 20px;
  height: 40px;
  z-index: 2;
  background-color: green;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

hr {
  height: 5px;
  color: solid;
}

.picture {
  width: 353px;
  height: 50px;
  text-align: center;
  border: 1px solid #000;
  position: absolute;
  bottom: 0;
}

.picture * {
  vertical-align: middle;
}

img {
  border: 4px solid #fff;
  border-radius: 10px;
  position: absolute;
}

@keyframes float-out {
  from {
  }

  35% {
    transform: rotate(20deg)translate(-20px);
  }

  80% {
    transform: rotate(-2deg);
  }

  to {
    left: calc(100% - 353px);
    transform: rotate(0deg);
  }
}

